import { uuid } from "@zindex/canvas-engine";
export class Keyframe {
    constructor(value, offset = 0, easing = null, id) {
        this.value = value;
        this.offset = offset;
        this.easing = easing;
        this.id = id || uuid();
    }
    clone(newId) {
        // T should be immutable
        return new Keyframe(this.value, this.offset, this.easing, newId ? null : this.id);
    }
    static fromJSON(json) {
        return new Keyframe(json.value, json.offset, json.easing);
    }
    withOffset(offset, newId) {
        return new Keyframe(this.value, offset, this.easing, newId ? null : this.id);
    }
    withValue(value, newId) {
        return new Keyframe(value, this.offset, this.easing, newId ? null : this.id);
    }
    withEasing(easing, newId) {
        return new Keyframe(this.value, this.offset, easing, newId ? null : this.id);
    }
    toJSON() {
        // we don't save id
        return {
            value: this.value,
            offset: this.offset,
            easing: this.easing,
        };
    }
}
