import { VectorAnimators } from "./CommonAnimators";
import { NumberAnimation, PositiveNumberAnimation } from "./CommonAnimations";
export const RegularPolygonAnimators = Object.assign(Object.assign({}, VectorAnimators), { radius: {
        id: 'radius',
        title: 'Radius',
        type: 'regular-polygon',
        create() {
            return new PositiveNumberAnimation('radius', 'regular-polygon');
        }
    }, cornerRadius: {
        id: 'polygon-corner-radius',
        title: 'Roundness',
        type: 'regular-polygon',
        create() {
            return new NumberAnimation('cornerRadius', 'regular-polygon');
        }
    } });
