import { VectorAnimators } from "./CommonAnimators";
import { PositiveNumberAnimation } from "./CommonAnimations";
export const EllipseAnimators = Object.assign(Object.assign({}, VectorAnimators), { width: {
        id: 'width',
        title: 'Width',
        type: 'ellipse',
        create() {
            return new PositiveNumberAnimation('width', 'ellipse');
        }
    }, height: {
        id: 'height',
        title: 'Height',
        type: 'ellipse',
        create() {
            return new PositiveNumberAnimation('height', 'ellipse');
        }
    } });
