import { MaskElement } from "@zindex/canvas-engine";
export class AnimatedMaskElement extends MaskElement {
    constructor() {
        super(...arguments);
        this._time = 0;
    }
    get time() {
        return this._time;
    }
    set time(value) {
        if (this._time !== value) {
            this._time = value;
            this.invalidate();
        }
    }
    getDocumentPictureOptions() {
        return this._time;
    }
    cloneCurrent() {
        const el = new AnimatedMaskElement(this._reference);
        el._time = this._time;
        return el;
    }
    applyJSON(json) {
        this._time = json.time || 0;
        super.applyJSON(json);
    }
    toJSON() {
        const json = super.toJSON();
        json.time = this._time;
        return json;
    }
}
